import React from "react"
import { graphql, Link } from "gatsby"
import Layout from "../components/layout"

export const query = graphql`
  query ($skip: Int!, $limit: Int!) {
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          frontmatter {
            title
            date(formatString: "MMMM Do, YYYY")
            slug
          }
          html
          excerpt
          fields {
            slug
          }
        }
      }
    }
  }
`

const BlogList = (props) => {

  const { currentPage, numPages } = props.pageContext;
  const isFirst = currentPage === 1;
  const isLast = currentPage === numPages;
  const prevPage = currentPage - 1 === 1 ? "/" : `/page/${(currentPage - 1).toString()}`;
  const nextPage = `/page/${(currentPage + 1).toString()}`;

  return (
    <Layout>
      <div className="posts">
        {props.data.allMarkdownRemark.edges.map((edge, index) => {
          const pattern = /\d{4}\-(0?[1-9]|1[012])\-(0?[1-9]|[12][0-9]|3[01])-/gm;
          const newslug = edge.node.fields.slug.match(pattern).join().replace(/\-/g, "/").slice(0, -1);
          return <div className="post" key={index}>
            <h1 className="post-title">
              <Link to={`/archive/${newslug}/${edge.node.frontmatter.slug}`}>
                {edge.node.frontmatter.title}
              </Link>
            </h1>

            <span className="post-date">{edge.node.frontmatter.date}</span>

            {edge.node.excerpt}

          </div>
        })}
      </div>
      <div className="pagination">
        {!isFirst && (
          <Link to={prevPage} rel="prev">
            ← Previous Page
        </Link>
        )}
        {!isLast && (
          <Link to={nextPage} rel="next">
            Next Page →
        </Link>
        )}
      </div>
    </Layout>
  )
}

export default BlogList;

